.events {
  color: $color-default-black;
  background-color: $color-default-white;
  padding-top: 50px;
  padding-bottom: 140px;

  @include vp-1023 {
    padding-top: 0;
    padding-bottom: 80px;
  }

  .container {
    display: flex;
    justify-content: space-between;

    @include vp-767 {
      flex-direction: column;
    }
  }

  &__col {
    @include vp-767 {
      width: 100%;
    }
  }

  &__col:first-child {
    margin-right: 34px;
    flex-grow: 1;

    @include vp-767 {
      margin-right: 0;
    }
  }

  &__day {
    display: block;
    margin-bottom: 10px;
    font-size: 24px;
    line-height: 32px;
    font-weight: 600;

    @include vp-1023 {
      font-size: 18px;
    }
  }

  &__meals {
    display: flex;
    align-items: center;
    margin-bottom: 20px;

    @include vp-1279 {
      margin-bottom: 15px;
    }
  }

  &__row {
    max-width: 470px;

    @include vp-1279 {
      max-width: 425px;
    }

    @include vp-767 {
      max-width: 100%;
    }
  }

  &__row::after {
    display: block;
    content: "";
    width: 100%;
    height: 1px;
    margin-bottom: 20px;
    background-color: $color-whisper;

    @include vp-1279 {
      margin-bottom: 15px;
    }
  }

  &__meal {
    font-size: 18px;
    line-height: 32px;
    font-weight: 400;

    @include vp-1279 {
      font-size: 16px;
      line-height: 140%;
    }

    @include vp-1023 {
      font-size: 12px;
    }
  }

  &__dot {
    display: inline-block;
    margin-right: 12px;
    width: 5px;
    height: 5px;
    background-color: $color-calico;
    border-radius: 50%;

    @include vp-1023 {
      margin-right: 6px;
    }

    &--first {
      margin-left: 12px;

      @include vp-1023 {
        margin-left: 6px;
      }
    }
  }

  &__decor {
    display: flex;
    position: relative;

    @include vp-767 {
      justify-content: center;
    }

    &::after {
      position: absolute;
      content: "";
      width: 471px;
      height: 502px;
      border: 1px solid $color-calico;
      top: 50px;
      left: 97px;

      @include vp-1279 {
        width: 330px;
        height: 351px;
      }

      @include vp-1023 {
        width: 230px;
        height: 280px;
        left: 80px;
      }

      @include vp-767 {
        height: 250px;
        left: 50%;
        transform: translateX(-50%);
        top: 30px;
      }
    }
  }

  &__decor-img {
    width: 323px;
    height: 486px;
    z-index: 1;

    &--1 {
      margin-right: 22px;
      padding-top: 152px;

      @include vp-1023 {
        padding-top: 100px;
      }

      @include vp-767 {
        padding-top: 60px;
      }
    }

    @include vp-1279 {
      width: 226px;
      height: 340px;
    }

    @include vp-1023 {
      width: 175px;
      height: 272px;
    }
  }
}
